   // ‰∫åÁª¥Á†ÅËé∑ÂèñÔºåÂêåÊó∂Ëé∑Âèñ‰∏Ä‰∏™È™åËØÅid‰ª•ÂêéÁî®
    async getQRCode() {
      try {
        const { data: res } = await this.$http.get('getQRCode')
        if (res.code !== 200) {
          // Áõ¥Êé•Ëµã‰∫àÁΩëÁªúÂõæÁâáÂú∞ÂùÄÂèØ‰ª•
          // Êú¨Âú∞ÈùôÊÄÅËµÑÊ∫êËµã‰∫àÂú∞ÂùÄ‰∏çÂèØ‰ª•ÔºåÈúÄË¶ÅÁî®requireÊù•Ëé∑Âèñ
          this.qrcodeInfo.qrcode = require('../assets/images/error.png')
          this.titleIsShow = true
          return this.$message.error('‰∫åÁª¥Á†ÅËé∑ÂèñÂ§±Ë¥•')
        } else {
          // ËØ∑Ê±ÇÊàêÂäüÊó∂
          res.data.qrcode = `data:image/png;base64,${res.data.qrcode}`
