      this.$refs.loginFormRef.validate(async valid => {
        console.log('aaa')
        if (!valid) return
        this.$router.push('/home')
      })
    }*/
    login() {
      let that = this
      //let loginForm = JSON.stringify(that.loginForm)
     // console.log(that.loginForm.password)
      this.$http.post('/login',
